.header__section {
    // overflow: hidden;
    background-color: var(--main-background-color);
    // position: fixed;
    top: 0;
    width: 100%;
    border-bottom: 2px solid var(--main-border-color);
    border-radius: 8px;
}

.header__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 60px;
}

