}

.header__nav-mobile {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    // @include desktop {
    //     display: none;
    // }
}

.nav-mobile__btn {
    border: transparent;
    background-color: transparent;
}

.nav-mobile__icon {
    cursor: pointer;
    color: var(--main-text-color);

    &:hover,
    &:focus {
        color: var(--second-text-color);
    }
}

.nav-mobile__burger {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    height: 100vh;
    width: 100%;
    z-index: 2;
    top: 0;
    left: 0;
    background-color: var(--main-background-color);
    transform: translateX(-100%);
    transition: 300ms all ease;

    &.open {
        display: flex;
        visibility: visible;
        transform: translateX(0);
        flex-direction: column;
        opacity: 1;
    }
}

.burger__box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 50px;
    height: 60px;
    padding-left: 20px;
    padding-right: 20px;

    // @include tablet {
    //     width: 768px;
    //     padding-left: 32px;
    //     padding-right: 32px;
    //     margin-left: auto;
    //     margin-right: auto;
    // }
}

.menu__logo {
    display: none;

    // @include tablet {
    //     display: block;
    //     margin-right: 60px;
    // }
}

.burger__close {
    background-color: transparent;
    border: 0;
    left: 90%;
    cursor: pointer;
    margin-left: auto;
    margin-top: 12px;
    color: var(--main-text-color) // @include tablet {
    //     margin-right: 0;
    //     margin-top: 0;
    // }
}

.close__icon {
    color: var(--main-text-color);
}

.overflow {
    overflow: hidden;
}

.nav-desktop__list {
    display: inline;
    // flex-direction: column;
    // gap: 32px;
    opacity: 0;
    transform: translateX(-300%);
    transition: 300ms transform ease;

    &.open {
        display: flex;
        transform: translateX(0);
        flex-direction: column;
        z-index: 3;
        opacity: 1;
        position: absolute;
        top: 180px;
        left: 20px;
    }

