}

.nav-desktop__item>a {
    position: relative;
    font-family: var(--base-font-family);
    font-size: 28px;
    line-height: 1.1;
    letter-spacing: 0.02em;
    padding: 0;
    color: var(--main-text-color);

    &:hover,
    &:focus {
        color: var(--second-text-color);
    }

    // @include desktop {
    //     opacity: 1;
