}

.nav-desktop__active {
    color: var(--second-text-color);
}

.favorite__list {
    position: absolute;
    //   visibility: visible;
    opacity: 0;
    z-index: 2;
    top: 60px;
    left: 0px;
    transform: scaleX(0);
    border-radius: 5px;
    background-color: var(--main-background-color);
    overflow: hidden;
    transition: 300ms all ease;

    &.active {
        visibility: visible;
        opacity: 1;
        transform: scaleX(1);
    }

