.header__search {
    position: absolute;
    pointer-events: none;
    opacity: 0;
    top: 90px;
    left: -12px;
    width: 100%;
    transform: translateX(-400%);
    transition: 300ms all ease;

    &.open {
        pointer-events: all;
        display: flex;
        z-index: 3;
        opacity: 1;
        transform: translateX(0);
        position: absolute;
    }

