}

.search__btn {
    width: 40px;
    height: 40px;
    position: relative;
    left: 30px;
    background-color: transparent;
    border-left: 0;
    border-right: 1px solid var(--btn-border-color);
    border-top: 0;
    border-bottom: 0;
    z-index: 2;
    cursor: pointer;
}

.search__icon {
    position: relative;
    margin-right: -30px;
    z-index: 1;
    top: 9px;

    // @include tablet {
    //     top: 0;
    // }
}

.search__input {
    width: 94%;
    height: 40px;
    border: 1px solid var(--btn-border-color);
    border-radius: 4px;
    padding-left: 44px;
    color: var(--btn-border-color);

    &::placeholder {
        color: var(--filter-text-color);
    }

    &:focus {
        outline: 1px solid var(--main-border-color);
    }
}

@media screen and (min-width: 768px) {
    .logo__img {
        width: 156px;
        height: 43px;
    }

    .burger__box {
        width: 768px;
        padding-left: 32px;
        padding-right: 32px;
        margin-left: auto;
        margin-right: auto;
    }

    .menu__logo {
        display: block;
        margin-right: 60px;
    }

    .burger__close {
        margin-right: 0;
        margin-top: 0;
    }

    .nav-desktop__list {
        gap: 40px;

        &.open {
            left: 45%;
        }
    }

    .header__search {
        opacity: 1;
        top: 0;
        left: 0;
        width: 288px;
        transform: none;
        transition: none;
        position: relative;
        display: flex;
        align-items: center;
        margin-right: 40px;
        margin-left: auto;
        margin-top: 10px;
        transition: none;
        pointer-events: all;

        &.open {
            display: none;
        }
    }

    .search__icon {
        top: 0;
    }
}

@media screen and (min-width: 1280px) {
    .header__nav-mobile {
        display: none;
    }

    .nav-desktop__list {
        display: flex;
        opacity: 1;
        position: relative;
        flex-direction: row;
        justify-content: center;
        transform: none;
        transition: none;
        align-items: center;
        margin-left: 43px;
        gap: 60px;
        margin-top: 15px;
        // font-size: 14px;
        // line-height: 1.3;
    }


    .nav-desktop__item>a {
        opacity: 1;
        flex-direction: row;
        justify-content: center;
        transform: none;
        transition: none;
        align-items: center;
        font-size: 18px;
        line-height: 1.3;

    }

    .nav-desktop__item--js {
        width: 210px;
        padding: 17px 0;
    }

    .favorite__list {
        top: 55px;
        left: 110px;
        // width: 240px;
        transform: translate(-50%, -50%) scaleX(0);
        overflow: hidden;
        transition: 300ms all ease;

        &.active {
            opacity: 1;
            transform: translate(0, 0) scaleX(1);
        }

    }

    .nav__icon {
        display: none;
    }

    .favorite__item {
        padding: 12px;
    }

    .favorite__item a {
        color: var(--main-text-color);
    }

    .header__search {
        position: relative;
        pointer-events: all;
        top: 0;
        left: 0;
        width: 288px;
        transform: translateX(0);
        opacity: 1;
        margin-right: 170px;

    }

}