*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --main-text-color: #202025;
  --filter-text-color: #5f6775;
  --main-background-color: #fcfcfc;
  --switch-light-color: #fd5103;
  --switch-dark-color: #5f6775;
  --second-text-color: #fd5103;
  --focus-text-bg-color: #fff;
  --abc-text-color: #5f6775;
  --favorite-bg-color: #fffcfa;
  --second-background-color: #fd5103;
  --main-border-color: #fe7031;
  --btn-border-color: #fd5103;
  --base-font-family: Poppins, sans-serif;
  --primary-font-family: Roboto, sans-serif;
}

[dark] {
  --main-text-color: #fcfcfc;
  --filter-text-color: #bdbdbd;
  --main-background-color: #202025;
  --switch-light-color: #fcfcfc;
  --switch-dark-color: #fd5103;
}

.header__section {
  background-color: var(--main-background-color);
  width: 100%;
  border-bottom: 2px solid var(--main-border-color);
  border-radius: 8px;
  top: 0;
}

.header__wrapper {
  height: 60px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo__img {
  width: 123px;
  height: 34px;
}

.header__nav-mobile {
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.nav-mobile__btn {
  background-color: rgba(0, 0, 0, 0);
  border: rgba(0, 0, 0, 0);
}

.nav-mobile__icon {
  cursor: pointer;
  color: var(--main-text-color);
}

.nav-mobile__icon:hover, .nav-mobile__icon:focus {
  color: var(--second-text-color);
}

.nav-mobile__burger {
  visibility: hidden;
  opacity: 0;
  height: 100vh;
  width: 100%;
  z-index: 2;
  background-color: var(--main-background-color);
  transition: all .3s;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.nav-mobile__burger.open {
  visibility: visible;
  opacity: 1;
  flex-direction: column;
  display: flex;
  transform: translateX(0);
}

.burger__box {
  height: 60px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
}

.menu__logo {
  display: none;
}

.burger__close {
  cursor: pointer;
  color: var(--main-text-color);
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  margin-top: 12px;
  margin-left: auto;
  left: 90%;
}

.close__icon {
  color: var(--main-text-color);
}

.overflow {
  overflow: hidden;
}

.nav-desktop__list {
  opacity: 0;
  transition: transform .3s;
  display: inline;
  transform: translateX(-300%);
}

.nav-desktop__list.open {
  z-index: 3;
  opacity: 1;
  flex-direction: column;
  display: flex;
  position: absolute;
  top: 180px;
  left: 20px;
  transform: translateX(0);
}

.nav-desktop__item > a {
  font-family: var(--base-font-family);
  letter-spacing: .02em;
  color: var(--main-text-color);
  padding: 0;
  font-size: 28px;
  line-height: 1.1;
  position: relative;
}

.nav-desktop__item > a:hover, .nav-desktop__item > a:focus, .nav-desktop__active {
  color: var(--second-text-color);
}

.favorite__list {
  opacity: 0;
  z-index: 2;
  background-color: var(--main-background-color);
  border-radius: 5px;
  transition: all .3s;
  position: absolute;
  top: 60px;
  left: 0;
  overflow: hidden;
  transform: scaleX(0);
}

.favorite__list.active {
  visibility: visible;
  opacity: 1;
  transform: scaleX(1);
}

.nav__icon {
  color: var(--main-text-color);
  fill: currentColor;
  margin-left: 10px;
}

.nav__icon:hover, .nav__icon:focus {
  color: var(--filter-text-color);
}

.favorite__item {
  background-color: var(--focus-text-bg-color);
  font-family: var(--base-font-family);
  letter-spacing: .02em;
  border: 1px solid var(--second-text-color);
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  font-size: 18px;
  line-height: 1.3;
}

.favorite__item:hover {
  background-color: var(--second-text-color);
}

.favorite__link:hover {
  color: var(--focus-text-bg-color);
}

.header__search {
  pointer-events: none;
  opacity: 0;
  width: 100%;
  transition: all .3s;
  position: absolute;
  top: 90px;
  left: -12px;
  transform: translateX(-400%);
}

.header__search.open {
  pointer-events: all;
  z-index: 3;
  opacity: 1;
  display: flex;
  position: absolute;
  transform: translateX(0);
}

.search__btn {
  width: 40px;
  height: 40px;
  border-left: 0;
  border-right: 1px solid var(--btn-border-color);
  z-index: 2;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border-top: 0;
  border-bottom: 0;
  position: relative;
  left: 30px;
}

.search__icon {
  z-index: 1;
  margin-right: -30px;
  position: relative;
  top: 9px;
}

.search__input {
  width: 94%;
  height: 40px;
  border: 1px solid var(--btn-border-color);
  color: var(--btn-border-color);
  border-radius: 4px;
  padding-left: 44px;
}

.search__input::-ms-input-placeholder {
  color: var(--filter-text-color);
}

.search__input::placeholder {
  color: var(--filter-text-color);
}

.search__input:focus {
  outline: 1px solid var(--main-border-color);
}

@media screen and (min-width: 768px) {
  .logo__img {
    width: 156px;
    height: 43px;
  }

  .burger__box {
    width: 768px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 32px;
    padding-right: 32px;
  }

  .menu__logo {
    margin-right: 60px;
    display: block;
  }

  .burger__close {
    margin-top: 0;
    margin-right: 0;
  }

  .nav-desktop__list {
    gap: 40px;
  }

  .nav-desktop__list.open {
    left: 45%;
  }

  .header__search {
    opacity: 1;
    width: 288px;
    pointer-events: all;
    align-items: center;
    margin-top: 10px;
    margin-left: auto;
    margin-right: 40px;
    transition: none;
    display: flex;
    position: relative;
    top: 0;
    left: 0;
    transform: none;
  }

  .header__search.open {
    display: none;
  }

  .search__icon {
    top: 0;
  }
}

@media screen and (min-width: 1280px) {
  .header__nav-mobile {
    display: none;
  }

  .nav-desktop__list {
    opacity: 1;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 60px;
    margin-top: 15px;
    margin-left: 43px;
    transition: none;
    display: flex;
    position: relative;
    transform: none;
  }

  .nav-desktop__item > a {
    opacity: 1;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    line-height: 1.3;
    transition: none;
    transform: none;
  }

  .nav-desktop__item--js {
    width: 210px;
    padding: 17px 0;
  }

  .favorite__list {
    transition: all .3s;
    top: 55px;
    left: 110px;
    overflow: hidden;
    transform: translate(-50%, -50%)scaleX(0);
  }

  .favorite__list.active {
    opacity: 1;
    transform: translate(0)scaleX(1);
  }

  .nav__icon {
    display: none;
  }

  .favorite__item {
    padding: 12px;
  }

  .favorite__item a {
    color: var(--main-text-color);
  }

  .header__search {
    pointer-events: all;
    width: 288px;
    opacity: 1;
    margin-right: 170px;
    position: relative;
    top: 0;
    left: 0;
    transform: translateX(0);
  }
}

