@media screen and (max-width: 767px) {
  .hero-btn {
    width: 93px;
    height: 34px;
    z-index: 1;
    color: var(--second-text-color);
    background-color: var(--main-background-color);
    border: 1px solid var(--btn-border-color);
    border-radius: 4px;
    padding-right: 70px;
    font-size: 16px;
    font-weight: 700;
    line-height: 135%;
    transition-property: color, background-color, box-shadow;
    transition-duration: .25s;
    transition-timing-function: cubic-bezier(.4, 0, .2, 1);
    display: block;
    position: absolute;
    top: 0;
    left: 0;
  }

  .hero-btn:focus {
    box-shadow: inset 0 0 4px rgba(0, 0, 0, .5);
  }

  .hero-btn--arrow {
    width: 10px;
    height: 1px;
    background-color: var(--second-background-color);
    transition-property: background-color;
    transition-duration: .25s;
    transition-timing-function: cubic-bezier(.4, 0, .2, 1);
    display: block;
    position: absolute;
    top: 50%;
    left: 70%;
    transform: rotate(45deg);
  }

  .hero-btn--arrow:after {
    content: "";
    width: 10px;
    height: 1px;
    background-color: var(--second-background-color);
    transition-property: background-color;
    transition-duration: .25s;
    transition-timing-function: cubic-bezier(.4, 0, .2, 1);
    display: block;
    position: absolute;
    bottom: 5px;
    left: 5px;
    transform: rotate(90deg);
  }
}

.active-btn {
  color: var(--focus-text-bg-color);
  background-color: var(--second-background-color);
}

.active-btn .hero-btn--arrow, .active-btn .hero-btn--arrow:after {
  background-color: var(--focus-text-bg-color);
}

.active-btn:focus {
  color: var(--second-text-color);
  background-color: var(--main-background-color);
}

.active-btn:focus .hero-btn--arrow, .active-btn:focus .hero-btn--arrow:after {
  background-color: var(--second-background-color);
}

